@import url(https://use.typekit.net/eah3vhx.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Vulf Mono";
  src: url(/static/media/Vulf_Mono-Italic_web.e9ac7880.ttf) format("ttf"),
    url(/static/media/Vulf_Mono-Italic_web.e937b3c6.woff) format("woff"),
    url(/static/media/Vulf_Mono-Italic_web.8cb3f0f5.woff2) format("woff2");
}
